<template>
  <div class="warpper">
    <div class="header">
      <span>年级管理</span>
      <div class="add_grade">
        <el-button type="success" @click="showAlert = true, type= 'add'">添加年级</el-button>
      </div>
    </div>
    <div class="table_wrap">
      <el-table :data="gradeList" border>
        <el-table-column label="年级名称" align="center" min-width="50">
          <template slot-scope="scope">
            <span>{{scope.row.gradeName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="班级个数" align="center" min-width="50">
          <template slot-scope="scope">
            <span>{{scope.row.classNum == 0 ? '无' : scope.row.classNum}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center" width="250">
          <template slot-scope="scope">
            <el-button size="mini" type="success" @click="editGrade(scope.row, scope.$index)">修改</el-button>
            <el-button size="mini" type="danger" @click="deleteGrade(scope.row, scope.$index)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <transition name="fade">
      <div class="cover_wrap" v-show="showAlert">
        <div class="cover_inner">
          <div class="grade_name">
            <span>年级名称：</span>
            <el-input v-model="gradeName" style="width:60%;" placeholder="请输入年级名称"></el-input>
          </div>
          <div>
            <el-button class="saveWrap" type="success" @click="gradeName = null, showAlert = false">关闭</el-button>
            <el-button class="saveWrap" type="success" @click="addGradeFun" style="margin-right: 10px;">保存</el-button>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
  import api from './api'
  export default {
    data () {
      return {
        gradeList: [],
        showAlert: false,
        gradeName: null,
        type: null,
        dataItem: {}
      }
    },
    mounted () {
      this.getGrade()
    },
    methods: {
      getGrade () {
        api.getGradeList({schoolId: this.$route.params.schoolId}, (data) => {
          this.gradeList = data
        }, (er) => {})
      },
      editGrade (item, index) {
        this.type = 'edit'
        this.dataItem = {
          data: item,
          index: index
        }
        this.showAlert = true
        this.gradeName = this.gradeList[index].gradeName
      },
      deleteGrade (item, index) {
        this.$confirm('您确定删除此年级吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          closeOnClickModal: false,
          type: 'error'
        }).then(() => {
          api.delGrade({schoolId: this.$route.params.schoolId, gradeId: item.gradeId}, (data) => {
            this.$message.success('删除成功')
            this.gradeList.splice(index, 1)
          }, (er) => {
            if (er.toUser !== undefined && er.toUser) {
              this.$message.error(er.message)
            } else {
              this.$message.error('删除失败')
            }
          })
        }).catch(() => {})
      },
      addGradeFun () {
        if (!this.gradeName) {
          this.$message.warning('请填写年级名称')
          return
        }
        if (this.type === 'edit') {
          api.editGrade({schoolId: this.$route.params.schoolId, gradeId: this.dataItem.data.gradeId, gradeName: this.gradeName}, (data) => {
            this.$message.success('修改成功')
            this.gradeList[this.dataItem.index].gradeName = this.gradeName
            this.showAlert = false
          }, (er) => {
            if (er.toUser !== undefined && er.toUser) {
              this.$message.error(er.message)
            } else {
              this.$message.error('修改失败')
            }
          })
        } else if (this.type === 'add') {
          api.addGrade({schoolId: this.$route.params.schoolId, gradeName: this.gradeName}, (data) => {
            this.$message.success('添加成功')
            this.getGrade()
            this.showAlert = false
            this.gradeName = null
          }, (er) => {
            if (er.toUser !== undefined && er.toUser) {
              this.$message.error(er.message)
            } else {
              this.$message.error('添加失败')
            }
          })
        }
      }
    }
  }
</script>
<style lang="less" scoped>
@import url('../../style/public.less');
.warpper{
  .header{
    .add_grade{
      float: right;
      margin-top: 10px;
    }
  }
  .table_wrap {
    margin-top: 5px;
  }
  .cover_wrap{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    background: rgba(0, 0, 0, .5);
    .cover_inner{
      padding: 20px 20px;
      background: #fff;
      width: 500px;
      border-radius: 5px;
      position: absolute;
      left: 50%;
      top: 50%;
      -webkit-transform:translate(-50%,-50%);
      -moz-transform:translate(-50%,-50%);
      -o-transform:translate(-50%,-50%);
      transform:translate(-50%,-50%);
      z-index: 101;
      .saveWrap {
        float: right;
      }
      .grade_name {
        margin:20px 20px;
      }
    }
  }
}
</style>



